#gallery-overlay {
  background-color: rgba(0, 0, 0, 0.8);
  position: fixed; /* Sit on top of the page content */
  width: 100%; /* Full width (cover the whole page) */
  height: 100%; /* Full height (cover the whole page) */
  z-index: 2000;
  margin-top: -54px;
}

#gallery-wrapper {
  background-color: rgb(64, 47, 31);
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
  min-width: 592px;
  /* width: 1392px; */

  /* height: 600px; */
  z-index: 3000;

  margin: 0;
}

#gallery-footer {
  border-top: solid 1px rgba(48, 35, 23, 0.8);
  height: 51px;
}

#media-content {
  min-height: 250px;
  position: relative;
}

#gallery-image {
  max-height: 837px;
  display: block;
  margin: 0 auto;
}

#gallery-tweet-tab {
  background-color: rgba(64, 47, 31, 0.8);
  /* background-color: pink; */
  height: 80px;
  position: absolute;
  width: 1392px;
  bottom: 0;
  z-index: 5000;
}

#gallery-action-bar {
  padding-top: 15px;
  width: 503px;
  height: 51px;
  margin: 0 auto;
  color: white;
  font-family: 'Segoe UI', Arial, sans-serif;
  font-weight: 700;
}

#gallery-retweet-counter {
  display: inline-block;
  width: 100px;
  margin-left: 8px;
  vertical-align: 20%;
  font-size: 12px;
  color: inherit;
}

#gallery-favorite-counter {
  display: inline-block;
  vertical-align: 20%;
  font-size: 12px;
  margin-left: 8px;
  width: 15px;
  color: inherit;
}

#button-grid {
  width: auto;
  grid-template-columns: 20px 20px;
  position: absolute;
  top: 50%;
  left: 50%;
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}

.icon-left-arrow:before {
  content: '\e903';

  margin-right: 150px;
}
.icon-right-arrow:before {
  content: '\e902';

  margin-left: 150px;
}

.icon-right-arrow:hover,
.icon-left-arrow:hover {
  cursor: pointer;
}

.icon-cross-sign:before {
  content: '\e904';
  display: flex;
  flex-direction: row-reverse;
  font-size: 20px;
  color: white;
  margin-top: 20px;
  margin-right: 20px;
}

.icon-cross-sign:hover {
  cursor: pointer;
}

#left-arrow-wrapper {
  width: 166px;
  heigth: 20px;
  display: inline-block;
}
