#login-overlay {
  width: 100%;
  height: 100%;
  display: grid;
  position: fixed;
  grid-template-rows: 1fr;
  grid-template-columns: 1fr 1fr;
}

#login-left-part {
  background-color: #1b95e0;
  height: 100%;
  width: 100%;
}

#splash-wrapper {
  margin-left: 50px;
  width: 650px;
  float: right;
}

#login-right-part {
  background-color: rgb(21, 32, 43);
}

#login-form {
  margin: 33px auto 0 auto;
  font-family: 'Segoe UI', Arial, sans-serif;
  font-size: 14px;
  max-width: 520px;
}

#login-email-input,
#login-password-input {
  -webkit-appearance: none;
  color: white;
  background-color: #10171e;

  padding: 13px 15px;
  border: solid 1px rgb(56, 68, 77);
  border-radius: 4px;
  font-family: inherit;
  margin-right: 12px;
  font-size: inherit;
  width: 175px;
}

#login-email-input {
  margin-left: -65px;
}

#login-button {
  -webkit-appearance: none;
  background-color: rgb(28, 41, 56);
  font-family: inherit;
  padding: 13px;
  font-size: 14px;
  color: rgb(29, 161, 242);
  font-weight: 700;
  border: solid 1px rgb(29, 161, 242);
  border-radius: 30px;
}

#login-button:hover {
  cursor: pointer;
}

#signup-intro {
  width: 310px;
  margin-top: 225px;
  font-family: 'Segoe UI', Arial, sans-serif;
}

#signup-twitter-icon {
  width: 46px;
}

#signup-header {
  font-family: inherit;
  font-size: 27px;
  line-height: 32px;
  color: rgb(255, 255, 255);
  text-rendering: optimizeLegibility;
  margin-top: 4px;
}

#logout-button {
  -webkit-appearance: none;
  background-color: rgb(0, 109, 191);
  color: white;
  border: none;
  border-radius: 100px;
  font-family: 'Segoe UI', Arial, sans-serif;
  font-size: 14px;
  font-weight: 700;
  width: 85px;
  float: right;
  margin-top: -45px;
  margin-right: 20px;
  height: 36px;
}

#logout-button:hover {
  cursor: pointer;
}

#signup-join {
  color: white;
  margin-top: 45px;
  font-size: 18px;
  line-height: 24px;
}

#signup-button {
  -webkit-appearance: none;
  background-color: rgb(0, 109, 191);
  color: white;
  border: none;
  border-radius: 100px;
  font-family: inherit;
  font-size: 14px;
  font-weight: 700;
  width: 100%;
  height: 36px;
}

#signup-button:hover {
  cursor: pointer;
}

#signup-menu {
  background-color: #10171e;
  width: 400px;
  height: 340px;
  border: solid 1px rgb(0, 109, 191);
  border-radius: 5px;
  font-family: 'Segoe UI', Arial, sans-serif;
}

#signup-email-label {
  color: white;
  display: block;
  margin-left: 12px;
  margin-top: 10px;
  margin-bottom: 5px;
  font-weight: 600;
  margin-left: 23px;
}

#signup-email,
#signup-password,
#signup-cf-password {
  display: block;
  -webkit-appearance: none;
  color: white;
  background-color: rgb(21, 32, 43);
  padding: 13px 15px;
  border: solid 1px rgb(56, 68, 77);
  border-radius: 4px;
  font-family: inherit;
  margin-bottom: 20px;
  margin-left: 12px;
  font-size: inherit;
  width: 342px;
}

#signup-email:focus,
#signup-password:focus,
#signup-cf-password:focus {
  border-color: rgb(0, 109, 191);
}

#signup-password-label {
  display: block;
  color: white;
  margin-left: 23px;
  margin-bottom: 5px;
  font-weight: 600;
}

#signup-password {
  display: block;

  margin-left: 12px;
}

#signup-cf-password-label {
  display: block;
  color: white;
  margin-left: 12px;
  margin-bottom: 5px;
  margin-left: 23px;
  font-weight: 600;
}

#signup-cf-password {
}

#back-button {
  display: inline;
  color: rgb(0, 109, 191);
  font-family: inherit;
  font-weight: 500;
  font-size: 20px;
  margin: 20px 12px;
}

#back-button:hover {
  text-decoration: underline;
  cursor: pointer;
  color: white;
}

#signup-submit {
  -webkit-appearance: none;
  background-color: rgb(0, 109, 191);
  color: white;
  border: none;
  border-radius: 100px;
  font-family: inherit;
  font-size: 14px;
  font-weight: 700;
  width: 90px;
  margin-left: 227px;
  height: 36px;
}

#signup-wrapper {
  width: 400px;
  margin: 200px auto;
}

#signup-menu-header {
  color: white;
  font-size: 27px;
}
