body {
  height: 100%;
  background-color: #101922;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow: default;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
  font-family: 'icomoon';
  src: url('fonts/icomoon.eot?ok5gs5');
  src: url('fonts/icomoon.eot?ok5gs5#iefix') format('embedded-opentype'),
    url('fonts/icomoon.ttf?ok5gs5') format('truetype'),
    url('fonts/icomoon.woff?ok5gs5') format('woff'),
    url('fonts/icomoon.svg?ok5gs5#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

a,
u {
  text-decoration: none;
  color: inherit;
}

a:hover {
  text-decoration: underline;
  color: rgb(29, 161, 242);
}

.controls {
  outline: none;
}

[class^='icon-'],
[class*=' icon-'] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

img:hover {
  cursor: pointer;
}

#twitter-icon-wrapper {
  width: 30px;
  margin: 14px auto;
}

#navbar-twitter-icon {
  width: 21px;
}

#nav-bar {
  width: 100%;
  position: fixed;
  height: 46px;
  margin-top: -54px;
  background-color: rgb(28, 41, 56);
}

.media-length-four-hor-divider {
  height: 0.5px;
}

.media-length-three-container,
.media-length-four-container {
  height: 342px;
  margin-bottom: 8px;
  border-radius: 12px;
  overflow: hidden;
}

.media-length-four-image-one {
  height: 342px;
  width: 380px;
  margin-bottom: 7px;
  object-fit: cover;
  display: inline-block;
}

.media-length-four-image-two,
.media-length-four-image-three,
.media-length-four-image-four {
  height: 114px;
  width: 126.66667px;
  margin-bottom: 2px;
  object-fit: cover;
  display: block;
}

.media-length-four-second-container {
  display: inline-block;
  width: 100px;
}

.media-length-three-image-one {
  height: 342px;
  width: 337px;
  margin-bottom: 2px;
  object-fit: cover;
  display: inline-block;
}

.media-length-three-second-container {
  display: inline-block;
  width: 167px;
}

.media-length-three-image-two {
  display: block;
  height: 181.633px;
  width: 167.677px;
  object-fit: cover;
}

.media-length-three-hor-divider {
  height: 1px;
}

.media-length-three-image-three {
  display: block;
  height: 161.633px;
  width: 167.677px;
  object-fit: cover;
}

.media-length-three-vert-divider {
  width: 1px;
  display: inline-block;
}

.media-length-two-divider {
  display: inline-block;
  width: 1px;
}
.media-length-two-image-one,
.media-length-two-image-two {
  min-height: 300px;
  max-height: 336px;
  width: 252px;
  margin-top: -20px;
  object-fit: cover;
}

.tweet-image-container {
  border-radius: 12px;
  overflow: hidden;
  height: 280px;

  margin-bottom: 8px;
}

.attached-image {
  border-radius: 12px;
  max-width: 506px;
  max-height: 450px;
  margin-bottom: 5px;
}

.icon-favorite:before {
  content: '\e900';
  font-size: 20px;
  color: inherit;
}

.icon-retweet:before {
  content: '\e901';
  font-size: 20px;
}

.icon-favorite:hover {
  color: #e0245e;
  cursor: pointer;
}

.icon-retweet:hover {
  color: rgb(23, 191, 99);
  cursor: pointer;
}

.retweet-counter {
  display: inline-block;
  width: 50px;
  margin-left: 5px;
  vertical-align: 20%;
  font-size: 12px;
}

.favorite-counter {
  display: inline-block;
  vertical-align: 20%;
  font-size: 12px;
  margin-left: 5px;
  width: 35px;
}

#tweet-stream {
  width: 590px;
  /* height: 96.58vh; */
  overflow: hidden;
  background-color: #101922;

  border: solid 1px rgb(56, 68, 77);
  margin: 54px auto 0 auto;
}
.tweet-top-bar {
  display: block;
}

.tweet-date-dot {
  margin: 5px;
  display: inline-block;
  /* visibility: hidden; */
  vertical-align: 22%;
}

.tweet {
  display: grid;
  grid-template-columns: 1fr 506px;
  grid-template-rows: 1fr;
  margin-top: 0px;
  padding-top: -20px;
  width: 590;
  border-bottom: solid 1px rgb(56, 68, 77);
  background-color: #15202b;
  color: white;
  font-family: 'Segoe UI', Arial, sans-serif;
  font-size: 14px;
  line-height: 20px;
}
.tweet-action-bar {
  color: rgb(136, 153, 166);
  font-weight: 700;
  margin-bottom: 5px;
}

.avatar-profile-anchor {
  height: 48px;
}

.tweet-name {
  font-weight: 700;
  display: inline-block;
  font-size: 14px;
}

.tweet-date {
  display: inline-block;
  color: rgb(136, 153, 166);
}

.tweet-at {
  margin-left: 5px;
}

.tweet-at,
.tweet-handle {
  font-size: 14px;
  color: rgb(136, 153, 166);
  display: inline-block;
}

/* .tweet:hover {
  cursor: pointer;
  background-color: #101922;
} */

.attached-image {
  border-radius: 12px;
  max-width: 506px;
  max-height: 450px;
  margin-bottom: 5px;
}

.example-enter {
  max-height: 0;
  opacity: 0.01;
}

.example-enter.example-enter-active {
  opacity: 100;
  max-height: 600px;
  transition: opacity 1.2s cubic-bezier(0.54, -0.22, 0.58, 0.59),
    max-height 1s ease-in-out;
}

.avatar {
  border-radius: 50%;
  margin: 12px 0 0 12px;

  display: block;
}

.tweet-content {
  margin-top: -5px;
  margin-left: -15px;
  display: block;
  width: 506px;
}

.tweet-text {
  margin-top: -14px;
}
